@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Roboto&family=STIX+Two+Text:ital@1&display=swap);
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
    font-family: 'STIX Two Text', serif;
}

.aboutUs {
    width: 65%;
    color: #6b6b6b;
    margin-top: 0;
    padding: 0 10px;
    font-family: 'Roboto', sans-serif;
}


.photos {
    display: flex;
    justify-content: space-around;
    width: 65%;
    margin-bottom: 20px;
}
.photos .we {
    width: 400px;
    border-radius: 50%;
}

.otherPhoto {
    width: 490px;
    margin: 10px;
}

.buket {
    width: 310px;
}

#borderLeft {
    padding-right: 0;
    padding-left: 20px;
    margin-right: 10px;
    border-right: none;
    border-left: 1px solid grey;;
}

.smaller {
    text-align: justify;
}
.smaller b {
    color: black;
}
.danil {
    width: auto;
}

#borderLeft b {
    cursor: pointer;
    color: #70A076;
}
.animate__animated  {
    width: 65%;
}
#column {
    flex-direction: column;
}
.left {
    float: left;
}


#moments {
    margin-bottom: 45px;
}

.grey {
    background-color: #e8e8e8;
    padding: 40px;
}
.grey h2, .white h2 {
    margin-top: 0;
}
.counterContainer {
    display: flex;
    width: 60%;
    justify-content: space-around;
}
.time {
    display: block;
    padding: 30px 35px;
    color: #70A076;
    font-size: 35px;
    min-width: 70px;
    font-weight: 400;
    background-color: #f5f5f5;
    text-align: center;
}

.time span {
    font-size: 20px;
    color: #6b6b6b;
    font-weight: 700;
}

.white {
    flex-direction: row;
    justify-content: center;
    padding: 100px 20px 20px;
}

.white p {
    width: 450px;
    margin-right: 60px;
    font-size: 22px;
    border-right: 1px solid grey;
    margin-top: 0;
    margin-left: 10px;
    padding-right: 10px;
}
.white #map {
    width: 450px;
    border: 5px solid #70A076;
}

.white #map:hover {
    cursor: pointer;
    box-shadow: -1px 5px 14px 3px rgba(0,0,0,0.46);
}

.castleIcon {
    width: 45px;
    float: left;
    margin-right: 10px;
}
.location {
    align-items: flex-start;
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    background-color: #70A076;
    border-radius: 0 0 10px 10px;
}
.footer img {
    width: 46px;
    margin-left: 20px;
}

